* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* Media query */
@media screen and (max-width: 1100px) {
  .navbar {
    padding: 1rem 2rem !important;
  }
  .mid {
    width: 100%;
    height: 80vh !important;
  }
}

.overlay {
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100vh;
  object-fit: cover;
  position: absolute;
  background-position: center;
  z-index: -1;
}

.navbar {
  background: rgba(0, 0, 0, 0.2);
  padding: 1rem 8rem;
}

.navbar-brand {
  font-size: 1.4rem;
  font-weight: 700;
}

.signin{
  color: #fff;
  font-size: 1.1rem;
  padding: 0 0.8rem;

}

#navbarSupportedContent > ul > li:nth-child(n) > a {
  color: #fff;
  font-size: 1.1rem;
  padding: 0 0.8rem;
}

#navbarSupportedContent > ul > li:nth-child(n) > a:hover,
#navbarSupportedContent > ul > li:nth-child(n) > a:active {
  color: #00bf85;
}

.signin {
  border-radius: 30px;
  padding: 0.4rem 1.4rem;
  font-weight: 500;
  text-transform: capitalize;
  background: #00bf85;
}
.signin:hover {
  background: #fff;
}

.mid {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mid video {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  position: absolute;
  z-index: -1;
}

.mid .hero {
  position: relative;
}

.mid .hero h3 {
  text-transform: capitalize;
}

.mid .hero p {
  text-transform: capitalize;
  width: 55%;
  font-size: 1.1rem;
  letter-spacing: 0.2rem;
  padding-bottom: 1rem;
}

.mid .hero a {
  border-radius: 30px;
  padding: 0.6rem 1.4rem;
  font-weight: 600;
  text-transform: capitalize;
  background: #00bf85;
}
.mid .hero a:hover {
  background: #fff;
}

.about {
  background: #000;
}

.about .text {
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.about .text h6 {
  text-transform: uppercase;
  color: #00bf85;
  font-weight: 800;
  font-size: 1rem;
  letter-spacing: 2px;
}

.about .text h2 {
  text-transform: capitalize;
  color: #f7f7f4;
  font-weight: 700;
  font-size: 2.7rem;
}

.about .text p {
  color: #f7f7f4;
  font-weight: 400;
  font-size: 1.1rem;
  letter-spacing: 0.5px;
}

.about .text a {
  color: #111;
  border-radius: 30px;
  padding: 0.6rem 1.4rem;
  font-weight: 600;
  background: #00bf85;
}
.about .text a:hover {
  color: #000;
  background: #fff;
}
